import { Component, OnInit, Inject, Injectable } from '@angular/core';
import { MatIconRegistry } from "@angular/material/icon";
import { DomSanitizer } from "@angular/platform-browser";
import { Router } from "@angular/router";
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { HttpClient } from '@angular/common/http'

export interface Connected {
  state: boolean;
}

@Component({
  selector: 'app-youtube',
  templateUrl: './youtube.component.html',
  styleUrls: ['./youtube.component.css']
})
export class YoutubeComponent implements OnInit {
  state: boolean = true;
  isExpanded: boolean = true;
  title: string = 'Dashboard';

  constructor(private matIconRegistry: MatIconRegistry, private domSanitizer: DomSanitizer, private router:Router, public dialog: MatDialog){
    this.matIconRegistry.addSvgIcon(
      "Facebook",
      this.domSanitizer.bypassSecurityTrustResourceUrl("../assets/Facebook_Logo.svg")
    );
    this.matIconRegistry.addSvgIcon(
      "Instagram",
      this.domSanitizer.bypassSecurityTrustResourceUrl("../assets/Instagram_Logo.svg")
    );
    this.matIconRegistry.addSvgIcon(
      "Twitter",
      this.domSanitizer.bypassSecurityTrustResourceUrl("../assets/Twitter_Logo.svg")
    );
    this.matIconRegistry.addSvgIcon(
      "Youtube",
      this.domSanitizer.bypassSecurityTrustResourceUrl("../assets/Youtube_Logo.svg")
    );
    this.matIconRegistry.addSvgIcon(
      "Home",
      this.domSanitizer.bypassSecurityTrustResourceUrl("../assets/Home_Logo.svg")
    );
  }

  ngOnInit(): void {
  }

  goToPage(PageName:string):void{
    this.router.navigate([`${PageName}`]);
  }

  openDialog(): void {
    const dialogRef = this.dialog.open(DialogConnection, {
      width: '250px',
      data: {state: this.state}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.state = result;
    });
  }
}

@Component({
  selector: 'dialog-connection',
  templateUrl: 'dialog-connection.html',
})
export class DialogConnection {

  readonly ROOT_URL = 'http://localhost:8080'
  posts: any;

  constructor(
    public dialogRef: MatDialogRef<DialogConnection>, private http: HttpClient,
    @Inject(MAT_DIALOG_DATA) public data: Connected) {}

  onOkClick() {
    this.dialogRef.close();
    return this.http.get(this.ROOT_URL + '/login')
  }
}